.header-wrapper{
    padding: 0;
    margin: 0;
    padding-left: 10px;
    padding-right: 10px;
    background-color: black;
    width: 100%;
    height: 800px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("./pics/portada_2.jpg");
    background-size: cover;
    z-index: 1;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .header-wrapper {
        background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("./pics/portada_3.jpg");
    }
  }
.header-wrapper h1{
    color: aliceblue;
    font-family: 'Titillium Web';
    z-index: 2;
    font-size: 4em;
}
.header-wrapper h3{
    color: aliceblue;
    font-family: 'Titillium Web';
    font-weight: 900;
    font-size: 3em;
}
.cards-wrapper{
    padding: 10px 30px;
    display: flex;
    justify-content: space-between;
}
.float-buttons{
    position: absolute;
    top:10px;
    right: 10px;
}
